.asset-logo {
    position: relative;
    display: flex;

    &__logo {
        height: 24px;
        width: 24px;
        background-size: 100% 100%;
        text-align: center;
        border-radius: 50%;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        position: relative;
        margin-right: 10px;
    }
}
