.btn {
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 16px;
    border-radius: 4px;
    border: solid 1px #5a81ea;
    background-color: transparent;
    color: #5a81ea;
    cursor: pointer;
    &.btn_primary {
        color: #fff;
        background-color: #5a81ea;
        &[disabled] {
            background: #bacaf4;
            cursor: default;
            border-color: #bacaf4;;
        }
        &:hover {
            background-color: #3c6ae4;
            &[disabled] {
                background: #bacaf4;
                cursor: default;
            }
        }
    }
    &.big {
        height: 56px;
        width: 100%;
        font-size: 18px;
        font-weight: 500;
    }
    &.btn_transparent {
        &:hover {
            color: #fff;
            background-color: #5a81ea;
        }
    }
}
